#loading,
.circle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #222;
}

#loading {
  height: 80%;
  width: 100%;
  position: fixed;
  -webkit-animation: fadeout .25s 2.5s ease-in forwards;
          animation: fadeout .25s 2.5s ease-in forwards;
}

.circle {
  background: #F94E6D;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 50%;
  margin: .5rem;
}

.circle:nth-child(1) {
  opacity: .5;
  -webkit-animation: fadeinout .6s ease-in-out 5 both;
          animation: fadeinout .6s ease-in-out 5 both;
}

.circle:nth-child(2) {
  opacity: .5;
  -webkit-animation: fadeinout .6s .15s ease-in-out 5 both;
          animation: fadeinout .6s .15s ease-in-out 5 both;
}

.circle:nth-child(3) {
  opacity: .5;
  -webkit-animation: fadeinout .6s .3s ease-in-out 5 both;
          animation: fadeinout .6s .3s ease-in-out 5 both;
}

@-webkit-keyframes fadeinout {
  20% {
    opacity: 1;
  }
}

@keyframes fadeinout {
  20% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  to {
    opacity: 0;
  }
}

@keyframes fadeout {
  to {
    opacity: 0;
  }
}


